<template>
  <div class="body mt-16">
      <v-container>
          <v-row app>
              <v-col cols="5">
                  <h3 class="my-4">Maximum Number of days stayed</h3>
                  <v-select label="Select" :items=days :rules="[rules.required]"  v-model="day" outlined dense>Select</v-select>
                  <h3 class="ml-13">For the following, enter "Yes" if one</h3>
                  <h3 class="my-5">Myocardial Infraction, Cerebrovascular Disease, Vascular Disease, Diabetes without complications :</h3>
                  <v-select :items=stays label="Select" :rules="[rules.required]"  v-model="disease1" outlined dense></v-select>
                  <h3 class="my-5">Dementia, Connective Tissue Disease :</h3>
                  <v-select :items=stays label="Select" :rules="[rules.required]"  v-model="disease2" outlined dense></v-select>
                  <h3 class="mb-10 mt-12">Metastatic Solid Tumor:</h3>
                  <v-select :items=stays label="Select" :rules="[rules.required]"  v-model="disease3" outlined dense></v-select>
              </v-col>
              <v-col cols="6" class="mr-16">
                  <h3 class="my-4">Is the patient admitted through Emergency block</h3>
                  <v-select label="Select" :rules="[rules.required]" :items=stays  v-model="emergency" outlined dense></v-select>
                  <h3>of the disorder/disease is applicable :</h3>
                  <h3 class="my-5">Cogestive Heartfailure , Diabetes with end organ damage , Chronic Pulmonary Disease , Mid Liver or Renal Disease , Any Tumour :</h3>
                  <v-select :items=stays :rules="[rules.required]" label="Select" v-model="disease4" outlined dense></v-select>
                  <h3 class="my-5">AIDS, Severe Liver or Renal Disease :</h3>
                  <v-select :items=stays :rules="[rules.required]" label="Select"  v-model="disease5" outlined dense></v-select>
                  <h3 class="mt-5">How many times did the patient visit the emergeny ward</h3>
                  <p>Note: Duration is within 6 months and 4 is maximum</p>
                  <v-text-field :items=stays :rules="[rules.required, rules.min]" label="Enter a number" v-model="times" outlined dense></v-text-field>
                  
              </v-col>
          </v-row>
      </v-container>
          <v-btn @click="calculate">Get Results</v-btn>
           <v-snackbar color="red" v-model="snackbar" :timeout="4000" :top="true">
          <span>Fill all the required fields </span>
          <template v-slot:action="{ attrs }">
          <v-btn text @click="snackbar=false" v-bind="attrs" color="white">close</v-btn>
          </template>
      </v-snackbar>
      <v-overlay :absolute="absolute" :value="overlay">
          <v-card min-width="150px" height="180px" class="white black--text" >
              <div style="text-align:center" class="pt-4">
                <h2>Result :</h2>
                <v-divider></v-divider>
                <h4>Score : {{result}}</h4>
                <h4 :class="condition">Condition : {{condition}}</h4>
                <v-btn @click="end" class="blue" style="right:230px;" >Ok</v-btn>
              </div>   
          </v-card>
      </v-overlay>
       </div>
</template>

<script>
export default {
 data(){
     return {
         days:['1','2','3','6','13',"more than 14"],
         dayval:{'1':1,'2':2,'3':3,'6':4,"13":5,"more than 14":7},
         stays:["Yes","No"],
         val:{"Yes":1,"No":0},
         day:null,
         condition:null,
         disease1:null,
         disease2:null,
         disease3:null,
         disease4:null,
         disease5:null,
         emergency:null,
         times:null,
         snackbar:false,
         result:0,
         absolute:true,
         overlay:false,
         rules: {
          required: value => !!value || 'Required.',
          min: v => v <= 4 || 'Enter a value below or equal to 4'
        }
     }
 },
 methods:{

     calculate:function(){
         
         if(this.day==null || this.disease1==null || this.disease2==null || this.disease3==null || this.disease4==null || this.disease5==null || this.emergency==null || this.times==null || isNaN(this.result)){
             this.snackbar=true
             this.overlay=false
         }
         else{
             this.disease1=this.val[this.disease1]
             this.disease2=this.val[this.disease2]
             this.disease3=this.val[this.disease3]
             this.disease4=this.val[this.disease4]
             this.disease5=this.val[this.disease5]
             this.emergency=this.val[this.emergency]
             this.times=parseInt(this.times)
             var c=0
             console.log(this.disease1+(this.disease2*2)+(this.disease3*3)+(this.disease4*4)+(this.disease5*6))
             if((this.disease1+(this.disease2*2)+(this.disease3*3)+(this.disease4*4)+(this.disease5*6))>4) {c=4}
             this.result=this.dayval[this.day]+this.emergency+this.times+c
             if(this.result<5) this.condition="LOW"
             else if(this.result<10) this.condition="MODERATE"
             else this.condition="HIGH"
             console.log(this.dayval[this.day])
             console.log(this.emergency)
             console.log(this.times)
             console.log(c)
             this.overlay=true
            //  alert(this.result)


         }

     },
     end:function(){
             this.disease1=null
             this.disease2=null
             this.disease3=null
             this.disease4=null
             this.disease5=null
             this.emergency=null
             this.day=null
             this.times=null
             this.overlay=false
     }
    
 }
}
</script>

<style scoped>
.v-btn{
    margin-left:450px;
    margin-top: 30px;
    margin-bottom: 40px;
}
#message{
    text-align: center;
}
.message{
   margin-left:450px;
}
.LOW{
    color:green;
}
.MODERATE{
    color:orange;
}
.HIGH{
    color:red;
}
</style>